@import "../shared/variables";

.WebLinks {
    margin-top: 4.75rem;
    .Links {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        div{
            display: inherit;
            flex-direction: column;
            align-items: center;
            // z-index: 10;
            position: relative;
        }
        a{
            display: inherit;
            height: 3.75rem;
            width: 3.75rem;
            border-radius: 100%;
            text-decoration: none;
            justify-content: center;
            align-items: center;
            border-width: 1.7px;
            border-color: $primary $white $white $primary;
            border-style: solid;
            background-color: transparent;
            transition: all 0.4s;
            i{
                font-size: 40px;
                display: inline;
                // transition: transform 0.1s;
            }
            &:hover{
                // border: 1.7px solid transparent;
                // i{
                //     transform: scale(1.5);
                // }
                border-radius: 0;
                border-color: transparent transparent $primary transparent;
            }
        }
    }
}

.fa-github{
    color: #FFFFFF;
}
.fa-linkedin{
    color: #0E76A8
}
.fa-envelope{
    color: #FFFFFF;
}

span.ToolTip {
    color: $white;
    background-color: $primaryBackground;
    margin-top: 5px;
    padding: 2px 10px;
    border: 1px solid $white;
    // border-top: 1px solid $primary;
    opacity: 0;
    transition: opacity 0.4s;
    &:hover{
        opacity: 1;
    }
}

.Links div:hover .ToolTip {
    opacity: 1;
}
