@import "../shared/variables";

.Intro {
    text-align: center;
    // position: relative;
    // z-index: 10;
}

.TypedHeader {
    position: relative;
}

div.Intro > h1{
    font-size: 4.2rem;
    font-family: $fancyFont;
    font-weight: inherit;
}

div.Intro > div{
    background-image: linear-gradient(to right, transparent, $primaryBackground 15%, $primaryBackground 85%, transparent);
    padding: 1px 0;
    display: inline-block;
    // z-index: 10;
    position: relative;
}

div.Intro > div > p{
    font-size: 1.3rem;
    text-align: inherit;
}

.noTypedjsBlink {
    animation: none !important;
}