@import "./shared/variables";

.App {
  color: $white;
}

.Flex-Box {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.Flex-Box > div {
  flex: 1 1 0
}

.Highlight {
  color: $primary;
}

.Highlight-2 {
  color: red;
}

.Container {
  min-height: 100vh;
  height: 100%;
  z-index: 2;
}

.BGOverlay {
  background-image: url(/img/bg-forest.jpg);
  opacity: 0.2;
  background-attachment: fixed;
  background-size:cover;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
}

.Left-Container {
  border-right: 1px solid red;
}

.Right-Container {
  border-left: 1px solid red;
}
