@import "../shared/variables";

.Blockquote-Container {
  background: $primaryBackground;
  width: 80%;
  margin: 2rem auto 0;
  blockquote {
    display: block;
    border-width: 2px 0;
    border-style: solid;
    border-color: $primary;
    padding: 1.5rem 0 1.5rem;
    margin: 7rem 0; 
    position: relative;
    text-align: center;
    font-size: 1.3rem;
    &::before {
      content: '\201C';
      position: absolute;
      transform: translate(-50%, -50%);
      font: 10rem 'Font Awesome';
      color: red;
    }
    &::after {
      content: "\2013 \2003" attr(cite);
      display: block;
      text-align: right;
      font-size: 1rem;
      color: red;
      padding-right: 1rem;
    }
    p:last-child {
      margin-bottom: 0; 
    }
  }
}
